<template>
    <div class="abnormaldata full">
        <head-top goBack = "true">
            <div slot = "head_search" class = "filter-box">
                <date-filter></date-filter>
            </div>
            <div slot = "save" class="search-icon"></div>
        </head-top>
        <div class = "section flex_col_left">
            <div class="font-blue flex_between padding_tb bgf">
                <div class="flex1">{{ldbmc}}<i class="xl-blue"></i></div>
                <div class="flex1">{{status}}<i class="xl-blue"></i></div>
            </div>
            <div class="dates-box"></div>
        </div>
    </div>
</template>

<script>
import headTop from '@/components/header/mainHeader'
import dateFilter from '@/components/common/dateFilter'

export default {
    components:{
        headTop,
        dateFilter
    },
    data () {
        return {
            ldbmc:'所有台区',
            status:'状态'
        }
    }
}
</script>

<style scoped lang = "scss">
@import '@/style/mixin.scss';
.filter-box{
   @include wh(100%,100%)
}
.search-icon{
    position: absolute;
    right: 0.3rem;
    @include wh(0.56rem,0.58rem)
    @include bg('../../images/common/sousuo.png')
}
.xl-blue{
    display: inline-block;
    margin-left: 0.11rem;
    margin-bottom: 0.05rem;
    @include wh(0.36rem,0.2rem)
    @include bg('../../images/common/downblue.png')
}
</style>


